import uniStatusBar from "@/components/uni-status-bar/uni-status-bar"
import uniLoadMore from "@/components/uni-load-more/uni-load-more.vue"
export default{
	components:{
		uniStatusBar,
		uniLoadMore
	},
	data(){
		return{
			logined:false,
			uid:'',
			token:'',
			keyword:'',
			active:0,
			rangeActive:'',
			sh:'',
			dataList:[],
			page:1,
			pageSize:10,
			total:0,
			more:'',
			lat:'',
			lng:''
		}
	},
	computed: {
	     noMore () {
	       return this.dataList.length >= this.total
	     },
	   },
	   onLoad(p){
		   var that=this
		   var userInfo=uni.getStorageSync('userInfo')
		   if(userInfo!='' & userInfo!=null & userInfo!=undefined){
		   	this.logined=true
		   	this.uid=userInfo.uid
		   	this.token=userInfo.token
		   }else{
		   	this.logined=false
		   }
		   uni.$on('logined',function(){
		   	var userInfo2=uni.getStorageSync('userInfo')
		   	that.logined=true
		   	that.uid=userInfo2.uid
		   	that.token=userInfo2.token
		   })
		   this.keyword=p.keyword
		   this.getPosition()
		   setTimeout(function(){
		   	that.$getHeight('#sv',(res) =>{
		   		that.sh=res
		   	})
		   },0)
	   },
	methods:{
		toggle(t){
			this.active=t
			this.reset()
			this.getList()
		},
		toggleRange(t){
			this.rangeActive=t
		},
		back(){
			uni.navigateBack({
				delta:1
			})
		},
		// 商品详情
		toDetail(id){
			uni.navigateTo({
				url:`/pages/index/goodsDetail?id=${id}`
			})
		},
		toShop(id){
			uni.navigateTo({
				url:`/pages/bussiness/shopPreview?id=${id}`
			})
		},
		addCollect(id){
			var ce=this.$operateInterceptor(this.logined)
			if(!ce){
				return ;
			}
			var that=this
			var params={
			  uid:this.uid,
			  token: this.token,
				goodsid:id
			}
			  var url='&r=api.member.cart.add'
			  this.$apiPost(url,params).then((res) =>{
					// that.options[2].info++
									that.$msg('添加成功')
			  })
		},
		reset(){
			this.page=1
			this.total=0
			this.dataList=[]
			this.more=''
			if(this.active!=2){
				this.rangeActive=''
			}else{
				this.rangeActive= this.rangeActive == 1 ? 2:1
			}
		},
		getList(){
			var that=this
			var params={
			  page:this.page,
			  pagesize: this.pageSize,
			  lng:this.lng,
			  lat:this.lat,
			  keywords:this.keyword,
			  type:this.active
			}
			if(this.rangeActive==1){
				params.type=3
			}else if(this.rangeActive==2){
				params.type=2
			}
			if(this.page==1){
				this.$loading()
			}
			  var url='&r=api.home.search'
			  this.$apiPost(url,params).then((res) =>{
				  that.total=res.data.total
				  that.dataList=that.dataList.concat(res.data.data)
				  that.more=''
				  if(that.page==1){
				  	uni.hideLoading()
				  }
			  })
		},
		toBottom(){
			if(this.noMore){
				this.more='noMore'
				return;
			}
			var that=this
			this.more='loading'
		  // setTimeout(function(){
			  that.page++
			  that.getList()
		  // },2000)
		},
		getPosition(){
			var that=this
			this.$loading()
			uni.getLocation({
				type: 'wgs84',
				success:(res) =>{
					console.log(res)
					that.lng=res.longitude
					that.lat=res.latitude
					that.getList()
				},
				fail:(reason) =>{
					console.log(reason)
					that.reset()
					that.lng=''
					that.lat=''
					that.getList()
				},
				complete:() =>{
					uni.hideLoading()
				}
			})
		},
		search(){
			this.active=0
			this.reset()
			this.getList()
		}
	}
}
